import {query_admin_user_auth} from "@/apis/getData";
import {setStore} from "@/assets/js/storage";

export default {
  methods:{
    query_admin_auth() {
      const params = {
        game_id: 2577,//游戏id, 取0表示全量游戏
        src_type: 0
      };
      query_admin_user_auth(params).then(response => {
        if (response.ret === 0) {
          if (response.data.level >= 0) {
            setStore("admin_level", response.data.level);
            if (this.$route.name === "LoginPage") {
              top.location.href = "/static/report_judge/admin/index.html#/?version=1";
            } else if (this.$route.name === "PermissionManage") {
              this.level = response.data.level;
            }
          } else {
            this.$loginOut();
            this.$onlyTxtPopShow(this.$errTxt.noAuth);
            //iframe 的父级页面跳转需要用top
            top.location.href = "/static/report_judge/admin/index.html#/login?version=2";
          }
          /*level 大于等于0表示用户具备登录权限， 其中：
          0: 登录、添加审核人员权限（官方合作人员权限）
          1：登录、添加审核人员、添加普通管理员权限（官方人员权限）
          注：目前权限1人员的添加，需通过后台开发人员进行*/
        } else if (response.ret === this.$code.LOGINTIMEOUT) {
          this.$loginOut();
        } else {
          this.$onlyTxtPopShow(this.$errTxt.networkErr);
        }
      })
    }
  }
}



