<template>
  <div class="wrap">
    <div class="cont">
      <h3 class="title-first"></h3>
      <!--<p class="explain">QQ账号密码登录</p>-->
      <div id="appendTo">
      </div>
      <!--<div class="login">
        <p>账号</p>
        <input type="text" name="qq" class="qq-login" />
        <p>密码</p>
        <input type="password" name="qq" class="qq-password" />
        <div class="kong"></div>
        <a href="auditPower.html" class="login-btn" onclick="PTTSendClick('btn','login','立即登录');">立即登录</a>
      </div>-->
      <h3 class="title-last"></h3>
    </div>
  </div>

</template>

<script>
    import query_admin_auth from "../mixins/query_admin_user_auth"
    export default {
      name: "LoginPage",
      mixins: [query_admin_auth],
      mounted(){
          this.loginIn();
      },
      methods:{
        loginIn(){
          if(this.$cookies.isKey("uin")){
            this.query_admin_auth();
          }else{
            this.$showPt();
          }
        },
      }

    }
</script>

<style scoped>
  body, html{height: 100%;}
  iframe + div {display: none}
  div{background: none;}
</style>



